:root {
  --primary-color-yellow: #f4c550;
  --secondary-color-yellow: #fbe9ba;
  --primary-color-green: #9fbaae;
  --secondary-color-green: #cbded3;
  --primary-color-black: #121417;
  --primary-color-grey: #F8F8F8;
}

*,
svg,
::before,
::after {
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  line-height: 1.25;
  margin: 0 auto;
  font-size: 45px;
  background-color: var(--primary-color-white);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

html {
  scroll-behavior: smooth;
}

address {
  font-style: normal;
}
